'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import supabase from '@/lib/supabaseBrowserClient'

export default function SetPasswordPage() {
  const router = useRouter()
  const [password, setPassword] = useState('')
  const [confirmPassword, setConfirmPassword] = useState('')
  const [message, setMessage] = useState('Verifying session...')

  useEffect(() => {
    supabase.auth.getSession().then(({ data }) => {
      if (!data.session) {
        setMessage('❌ Link expired or invalid. Try logging in again.')
      } else {
        setMessage('')
      }
    })
  }, [])

  const handleSetPassword = async (e: React.FormEvent) => {
    e.preventDefault()
    setMessage('Setting password...')
    if (!password || !confirmPassword) {
        setMessage('❌ Please fill in both fields.')
        router.replace('/login/set-password')
        return
    }
    if (password !== confirmPassword) {
        setMessage('❌ Passwords do not match.')
        router.replace('/login/set-password')
        return
    }
    const { error } = await supabase.auth.updateUser({ password })
    
    if (error) {
      setMessage('❌ Error setting password: ' + error.message)
    } else {
      setMessage('✅ Password set! Redirecting...')
      setTimeout(() => router.replace('/'), 1000)
    }
  }

  return (
    <div className="p-6 max-w-md mx-auto">
      <h1 className="text-2xl font-bold mb-4">Set Your Password</h1>

      {message && <p className="mb-4 text-sm text-gray-700">{message}</p>}

      {!message && (
        <form onSubmit={handleSetPassword} className="space-y-4">
          <input
            type="password"
            className="border px-3 py-2 w-full rounded"
            placeholder="Enter new password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
          <input
            type="password"
            className="border px-3 py-2 w-full rounded"
            placeholder="Confirm password"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            required
          />
          <button type="submit" className="bg-black text-white px-4 py-2 rounded w-full">
            Set Password
          </button>
        </form>
      )}
    </div>
  )
}